.BuildControls {
    -ms-flex-align: center;
        align-items: center;
    background-color: #CF8F2E;
    -webkit-box-shadow: 0 2px 1px #ccc;
            box-shadow: 0 2px 1px #ccc;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    margin: auto;
    padding: 10px 0;
    width: 100%;
}

@media (min-width: 501px) and (max-width: 1300px) {
    .GridOfBuildControls {
        -webkit-column-count: 2;
