.Price {
    font-size: 20px;
}

.Price strong{
    margin-left: 25px;
}


.OrderButton {
    background-color: #DAD735;
    border: 1px solid #966909;
    -webkit-box-shadow: 2px 2px 2px #966909;
            box-shadow: 2px 2px 2px #966909;
    color: #966909;
    cursor: pointer;
    font-family: inherit;
    font-size: 1.0em;
    margin-top: 8px;
    outline: none;
    padding: 8px 30px;
    width: 50%;
}

.OrderButton:hover, .OrderButton:active {
    background-color: #A0DB41;
    border: 1px solid #966909;
    color: #966909;
}

.OrderButton:disabled {
    background-color: #C7C6C6;
    border: 1px solid #ccc;
    color: #888888;
    cursor: not-allowed;
}

.OrderButton:not(:disabled) {
    -webkit-animation: enable 0.3s linear;
            animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}



