.NavigationItem {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    width: 100%;
    margin: 10px 0;
}

.NavigationItem a {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: #8F5C2C;
    display: block;
    width: 100%;
    text-decoration: none;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
    color: #40a4c8;
}

@media (min-width: 500px) {
