        -ms-flex-align: center;
            align-items: center;
        display: -ms-flexbox;
        display: flex;
        height: 100%;
        margin: 0;
        width: auto;
    }

    .NavigationItem a {
        border-bottom: 4px solid transparent;
        color: white;
        height: 100%;
        padding: 16px 10px;
    }

    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active {
        background-color: #8f5c2c;
        border-bottom: 4px solid #40a4c8;
        color: white;
    }
}



